import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';

import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';
import * as _ from 'underscore';
import * as moment from 'moment';
@Component({
  selector: 'app-signuprequest',
  templateUrl: './signuprequest.component.html',
  styleUrls: ['./signuprequest.component.scss']
})
export class SignuprequestComponent implements OnInit {
  unitListData:any; 
  totalUserItems:any;
  unitFieldType:string = "unitno";
  unitOrder:boolean = false;
  isUserDataLoaded:boolean = false;
  isProfile:boolean = false;
  viewUserId:any;
  ItemUserStartIndex:any = 0;
  ItemUserEndIndex:any;
  itemUserLimit:any = 15;
  modalService:any;
  isMobile:boolean = false;
  columnField: any = {}
  selectAllUnapprovedUser:boolean = false;
  isUserSelected:boolean = false;
  unitData:string = "";
  selectedInput:string = "";
  ItemLogStartIndex:any = 0;
  ItemLogEndIndex:any;
  constructor( 
    private injector: Injector,
    public dialog: MatDialog,
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService, 
    private sharedService:SharedService,
    private cookieService:CookieService) { 
      this.modalService = this.injector.get(ModalService);
    }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.userService.getAllSignupUserRequestByApartmentId(params).subscribe((res : any) => {
     // this.userService.getAllSignupUserRequestByApartmentId(3).subscribe((res : any) => {  
    console.log(res);
      var unitLstData = res;
      this.unitListData = unitLstData.filter(itm=>itm.isActive);

      this.totalUserItems = this.unitListData.length;

      if(this.totalUserItems>this.itemUserLimit){
        this.ItemUserEndIndex = this.itemUserLimit;
      }
      else {
        this.ItemUserEndIndex = this.totalUserItems;
      }

      this.isUserDataLoaded = true;
    },
    error => {
      console.log(error);
    }
    );

    let unitParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.userService.getAllUnApprovedUsersByApartmentId(unitParams).subscribe((res : any) => {

      var unitListData = res;
      console.log(res);
     console.log(res.name);
     
    },
    error => {
      console.log(error);
    }
    );
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }
  selectColInput(value){
    console.log(value);
    this.selectedInput = value;
  }
  getLogIndexParams(event){
    this.ItemLogStartIndex = event.ItemLogStartIndex;
    this.ItemLogEndIndex = event.ItemLogEndIndex;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }
  getNotes(data){
    console.log(data);
    if(data === undefined || data.length == 0)
   return '';
   else 
   return data.notes;
 }
 getBlockName(data){
  var dt = JSON.parse(data);
  return dt.BlockName;
 }
 getUserType(data){
  var usrType =0;
  var dt = JSON.parse(data);
  usrType = dt.UserType;
  var usrName ="";
  if(usrType==0){
    usrName = "N/A";
  }else if(usrType == 2){
    usrName = "Tenant";
  }else if(usrType == 4){
    usrName = "Owner";
  }else if(usrType == 1){
    usrName = "Admin";
  }
  return usrName;
 }
 getUnitName(data){
  var dt = JSON.parse(data);
  return dt.UnitName;
 }
 getUserRole(role, index){
  if(role === undefined || role.length == 0){
    return '';
  }
  else {
    this.unitListData[index].roleName = role[0].roleName;
    return role[0].roleName;
  } 

  
}
onDateChange(event, type){

  if(event != null){
    this.selectedInput = type;
    this.columnField[type] = moment(event).format("DD/MM/YYYY");
  }
  else {
    this.columnField = {};
  }
  
}
getBookedDate(itr, date){

  _.each( this.unitListData, (obj, index) => {
       obj.facilityBookedForDate = moment(obj.bookedForDate).format("DD/MM/YYYY");
   });

   return moment(date).format("DD/MM/YYYY");
 }
getBlockNo(item, data){
  this.unitListData.map(obj=> {
    if(obj.id == item.id){
      obj.apartmentBlockNumber = data;
    }
  });
  return data;
}
getBlockUnitNo(item, data){
  this.unitListData.map(obj=> {
    if(obj.id == item.id){
      obj.apartmentBlockUnitNumber = data;
    }
  });
  return data;
}

viewUserInfo(id){
    
  this.isProfile = false;

  setTimeout(() => {

    this.isProfile = true;

  }, 10);

  setTimeout(() => {

    var elem = document.querySelector('.user-info-card');
    var scrollTo = elem.getBoundingClientRect().top - 100;

    window.scroll({
      top: scrollTo,
      behavior: 'smooth'
    });

  }, 100);


  this.viewUserId = id;

  this.router.navigate(['ams/unit&users/unapproved/basic', this.viewUserId]);

  

}
showConfirmModal(data) {
  this.modalService.showConfirmModal(data.signupUserRequestId);
  var params = {
    signupUserId:data.signupUserRequestId,
    apartmentId:data.apartmentId
  }
  this.userService.updateSignupUserRequestByApartmentId(params).subscribe((res : any) => {
  if(res.message){
    this.sharedService.setAlertMessage("Signup deleted successfully.");
    this.router.navigate(['ams/unit&users/usersignuprequest', this.viewUserId]);
  }
  },
  error => {
    console.log(error);
  }
  );
}

approveUsers() {
  this.isUserDataLoaded = false;
  _.each(this.unitListData, (item, index)=>{
      if(item.checked){
        this.userService.getUserById(item.id).subscribe((res:any) => {
            var user = res[0];
            user.isApproved = true;
            this.userService.updateUser(user).subscribe((res:any) => {
              
              this.unitListData.splice(index, 1);
              this.totalUserItems = this.unitListData.length;
              this.isUserDataLoaded = true;

            })
        });
      }
   })
}
@HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
      else
      this.isMobile = false;

  }
getUserIndexParams(event){
  console.log(event);
  this.ItemUserStartIndex = event.ItemUserStartIndex;
  this.ItemUserEndIndex = event.ItemUserEndIndex;
  this.itemUserLimit = event.ItemLimit;

  if(this.totalUserItems>this.itemUserLimit){
    this.ItemUserEndIndex = this.itemUserLimit;
  }
  else {
    this.ItemUserEndIndex = this.totalUserItems;
  }

}
isMobileView() {
  return window.innerWidth <= 767 ? 'table-responsive' : '';
}
getAllUnapprovedUser(){

  if(this.selectAllUnapprovedUser){
    _.each(this.unitListData, (item)=>{
      item.checked = true;
    })
    this.isUserSelected = true;
  }
  else{
    _.each(this.unitListData, (item)=>{
      item.checked = false;
    })
    this.isUserSelected = false;
  }

}
getSelectedUnapprovedUser(unit) {
  var length=0;
  _.each(this.unitListData, (item)=>{
      if(item.checked){
        length++;
      }
   })
  if(length>0){
    this.isUserSelected = true;
  }
  else{
    this.isUserSelected = false;
  }

  if(length==0){
    this.selectAllUnapprovedUser = false;
  }

  if(length == this.unitListData.length){
    this.selectAllUnapprovedUser = true;
  }
  
}
EditUserInfo(data){
  data.mode = true;
  this.modalService.showSignUpdetailsModal(data);
}
viewSignup(data){
  data.mode = false;
  this.modalService.showSignUpdetailsModal(data);
}
}
